<template>
  <div class="column-view">
    <div ref="topBar">
      <van-nav-bar
        title="上传图片"
        right-text="任务详情"
        left-text="返回"
        fixed
        left-arrow
        @click-left="onNavBarLeftPressed"
        @click-right="onNavBarRightPressed">
      </van-nav-bar>
    </div>
    <div class="under-nav top-section" v-if="missionDetail">
      <div style="display: flex;align-items: center;flex-wrap: wrap">
        <span>发布方：</span>
        <img class="head-img" :src="imgFullPath(missionDetail.publishUserImgUrl)" alt=""/>
        <span class="publish-name">{{missionDetail.publishUserName}}</span>
        <span class="right-text">编号：</span>
        <span>{{missionDetail.id}}</span>
      </div>
      <div style="display: flex;align-items: center;flex-wrap: wrap">
        <span>类别：</span>
        <span>{{getMissionTypeName()}}</span>
        <span class="right-text">单价：</span>
        <span>{{missionDetail.price / 100}}元</span>
      </div>
      <span>备注：</span>
      <span>{{missionDetail.remark}}</span>
    </div>
    <div>
      <div style="text-align: center">
        <span>验证图</span>
      </div>
      <van-swipe :autoplay="0" :width="vanSwipeWidth" :height="vanSwipeHeight" :loop="false"
                 :show-indicators="false">
        <van-swipe-item v-for="(item, index) in uploadImgArray" :key="index" @click="onExplainItemClick(index)"
                        class="swipe-item">
          <img class="swipe-img" style="position: relative;" :src="item" alt="">
          <div class="delete">
            <van-icon name="clear" size="30px"
                      style=""
                      @click.stop="onItemDeleteClick(index)"/>
          </div>
        </van-swipe-item>
        <van-swipe-item class="swipe-item" v-if="uploadImgArray.length < 5">
          <div class="swipe-img plus-div">
            <van-uploader :after-read="onRead" name="uploadLoader" :max-size="1048576"
                          @oversize="uploadImgOversize">
              <van-icon name="add-o" size="80px"/>
            </van-uploader>
          </div>
        </van-swipe-item>
      </van-swipe>
      <div style="text-align: center">
        <span>传图片，最多可上传5张</span>
      </div>
      <div style="text-align: center">
        <span>提示：请诚信做单，骗单将被罚款，严重者封号。</span>
      </div>
      <div style="text-align: center" v-show="missionDetail && missionDetail.textVerify">
        <input placeholder="请添加文字验证" v-model="textVerify">
      </div>
    </div>
    <div class="submit">
      <van-button type="danger" round size="large" class="left-button" @click="onNavBarLeftPressed">取消</van-button>
      <van-button type="primary" round size="large" @click="onCreateMissionButtonPressed">提交</van-button>
    </div>
  </div>
</template>

<script>
  import { FINISH_MISSION_URL, MISSION_DETAIL_URL } from "@/config/host"
  import { getPropertyById } from "@/service/property"
  import { Toast } from "vant"

  export default {
    name: "FinishMission",
    data () {
      return {
        missionId: null,
        missionDetail: null,
        vanSwipeWidth: 100,
        vanSwipeHeight: 130,
        uploadImgArray: [],
        textVerify: ""
      }
    },
    async mounted () {
      this.missionId = this.$route.query.id
      const result = await this.httpPost(MISSION_DETAIL_URL, { id: this.missionId })
      if (result.success) {
        this.missionDetail = result.data
      }
    },
    methods: {
      onNavBarLeftPressed () {
        this.$router.back()
      },
      onNavBarRightPressed () {
        this.$router.push({ name: "MissionDetail", query: { id: this.missionId } })
      },
      getMissionTypeName () {
        const p = getPropertyById(this.missionDetail.typePropertyId)
        return p.name
      },
      onRead (file, obj) {
        this.uploadImgArray.push(file.content)
      },
      uploadImgOversize () {
        Toast("单张图片最大尺寸为1MB")
      },
      onItemDeleteClick (itemIndex) {
        this.uploadImgArray.splice(itemIndex, 1)
      },
      async onCreateMissionButtonPressed () {
        if (!this.uploadImgArray || this.uploadImgArray.length === 0) {
          this.toastFail("请添加验证图")
          return
        }
        if (this.missionDetail.textVerify && !this.textVerify) {
          this.toastFail("请添加文字验证")
          return
        }
        const data = {
          missionId: this.missionId,
          verifyImgList: this.uploadImgArray,
          textVerify: this.textVerify
        }
        const result = await this.httpPostWithLoading(FINISH_MISSION_URL, data)
        if (result.success) {
          this.toastSuccess("任务上传完成，请等待审核")
          this.$router.back()
        }
      }
    }
  }
</script>

