      margin-left: 70px;
    }

    .head-img {
      width: 36px;
      height: 38px;
      border-radius: 18px;
    }
  }

  .swipe-img {
    width: calc(100% - 8px);
    height: calc(100% - 8px);
    border: 2px;
    border-radius: 10px;
    background-color: white;
    object-fit: cover;
  }

  .swipe-item {
    display: flex;
    align-items: center;
    justify-content: center;

    .delete {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 30px;
      height: 30px;
      background-color: white;
      position: absolute;
      border: 2px;
      border-radius: 50%;
      left: 0;
      top: 0
    }
